<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                设备详情
            </p>
        </section>
        <section class="middle">
            <div class="figure">
                <img :src="detail.img" alt="">
            </div>
            <p class="article-title" v-html="detail.name"></p>
            <p class="collect">
                <span v-if="detail.isCollection"  @click="recollect"><img width="15px" src="../../../assets/technology/icon_Collected@2x.png"><span style="font-size: 14px">已收藏</span></span>
                <span v-else @click="collect"><img width="15px" src="../../../assets/technology/icon_Uncollected@2x.png"><span style="font-size: 14px">收藏</span></span>
            </p>
        </section>
        <hr>
        <section class="bottom">
            <div class="cover">
                <ul>
                    <li>
                        <img src="../../../assets/technology/item_red@2x.png" alt="">
                        <p>所属类别:<span v-html="detail.instrumentsType"></span></p>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_blue@2x.png" alt="">
                        <p>所属领域:<span v-html="detail.genre"></span></p>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_blue@2x.png" alt="">
                        <p>设备来源:<span v-html="detail.source"></span></p>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_red@2x.png" alt="">
                        <p>设备厂商:<span v-html="detail.manufacturer"></span></p>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_red@2x.png" alt="">
                        <p>性能参数:<span v-html="detail.performanceParameter"></span></p>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_blue@2x.png" alt="">
                        <p>规格型号:<span v-html="detail.specifications"></span></p>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_blue@2x.png" alt="">
                        <p>主要功能:<span v-html="detail.majorFunction"></span></p>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_red@2x.png" alt="">
                        <p>主要技术指标:<span v-html="detail.technicalIndicators"></span></p>
                    </li>
                </ul>
            </div>
            <div class="title red-line">
                联系人信息
            </div>
            <div class="article linkman">
                <ul>
                    <li><span class="contact-key">联系人：</span><span class="contact-value" v-html="detail.contacts"></span></li>
                    <li><span class="contact-key">联系电话：</span><span class="contact-value" v-html="detail.phone"></span></li>
                    <li><span class="contact-key">联系邮箱：</span><span class="contact-value" v-html="detail.email"></span></li>
                    <li><span class="contact-key">通讯地址：</span><span class="contact-value" v-html="detail.address"></span></li>
                    <li><span class="contact-key">邮政编码：</span><span class="contact-value" v-html="detail.postalCode"></span></li>
                </ul>
            </div>
            <div class="title blue-line">
                简介
            </div>
            <div class="article" v-html="detail.intro">
            </div>
            <div class="title red-line">
                使用事项说明
            </div>
            <div class="article" v-html="detail.instructions">
            </div>
            <div class="title blue-line">附件下载</div>
            <div class="link" @click="download" v-html="detail.fileName">文件名</div>
        </section>
        <div class="button" @click="docking">
            <img src="@/assets/home/icon_shebeiyuyue@2x.png" alt="">
            设备预约
        </div>
        <md-dialog
                :closable="true"
                v-model="iconDialog.open"
                :btns="iconDialog.btns"
        >
            <img src="../../../assets/technology/img@2x.png" alt="">
            <p>预约申请已发送给设备单位</p>
        </md-dialog>
    </main>
</template>
<script>
import { docking, collect, recollect } from '@/apiUrl/technology'
import { getDetail } from '@/apiUrl/device'
import { recollectP } from "../../../apiUrl/activity";
import { Icon, Toast, ActivityIndicator, Landscape, TabBar, Swiper, SwiperItem, Dialog } from 'mand-mobile'
export default {
  name: 'news_detail',
  components: {
    [Icon.name]: Icon,
    [ActivityIndicator.name]: ActivityIndicator,
    [Landscape.name]: Landscape,
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [Dialog.name]: Dialog
  },
  data () {
    return {
      collected: false,
      current: 1,
      items: [{ name: 1, label: '成果简介' }, { name: 2, label: '应用说明' }, { name: 3, label: '效益分析' }],
      currentId: '',
      detail: {},
      iconDialog: {
        open: false
      }
    }
  },
  mounted () {
    this.currentId = this.$route.query.id
    this.getDetail()
  },
  methods: {
    getDetail () {
      getDetail({ id: this.currentId }).then(res => {
        this.detail = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    download () {
      window.open(this.detail.file)
    },
    docking () {
        this.$router.push({ path: '/submission/index1' ,query: { type : 3 } })
      // docking({ type: 3, infoId: this.currentId }).then(res => {
      //   // this.showMaskClosable = true
      //   this.iconDialog.open = true
      // }).catch(err => {
      //   Toast.failed(err)
      // })
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    },
      // 取消收藏
      recollect () {
          const args = {
              infoId: this.currentId,
              type: 3
          }
          recollectP(args).then(res => {
              Toast.succeed('收藏成功')
              this.getDetail()
              this.collected = true
          }).catch(err => {
              Toast.failed(err)
          })
      },
    // 收藏
    collect () {
      const args = {
        infoId: this.currentId,
        type: 3
      }
      collect(args).then(res => {
        Toast.succeed('收藏成功')
        this.getDetail()
        this.collected = true
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.$refs.swiper.goto(index)
    }
  }
}
</script>

