                    display flex
                    align-items center
                    color #333333
                    img
                        margin-right 10px
        hr
            border none
            border-bottom 24px solid #EEEEEE
            margin 30px auto
        .bottom
            margin 0 auto
            padding-bottom 150px
            width 690px
            .cover
                background rgba(255, 255, 255, 1)
                box-shadow: 1px 1px 8px 0px rgba(2, 25, 85, 0.2)
                border-radius 6px
                padding 16px 20px
                margin-bottom 40px
                ul
                    display flex
                    flex-wrap wrap
                    justify-content space-between
                    li
                        font-size 24px
                        color #666666
                        flex 0 0 50%
                        text-align left
                        margin 20px 0
                        display flex
                        justify-content left
                        img
                            width 26px
                            height 26px
                            margin-right 11px
                        p
                            text-align left
                            color #666666
                            span
                                color #333333
            .title
                width 690px
                height 66px
                line-height 66px
                color #333333
                font-size 28px
                text-indent 30px
                text-align left
                margin-bottom 30px
                background rgba(153, 153, 153, 0.1)
                .md-tab-bar
                    width 80%
                /deep/.md-tab-bar-item
                    height 66px!important
                    min-height 66px!important
            .red-line
                background rgba(153, 153, 153, 0.1) url("../../../assets/technology/line_red@2x.png") left center no-repeat
                background-size 13px 66px
            .article
                width 690px
                padding 28px 32px
                font-size 24px
                color #666666
                border 2px solid rgba(153, 153, 153, 0.3)
                border-radius 8px
                line-height 36px
